<script>
import LGTM from '@/components/LGTM.vue'

const BASE_URL = process.env.NODE_ENV === 'development' ? 'http://localhost:5000' : ''
const FORM_STRUCTURE = {
  mood: '',
  mood_vs_last_week: '',
  fatigue: '',
  fatigue_vs_last_week: '',
  workload: '',
  proximity_project: '',
